#logo{
    display:flex;
    justify-content:center;
    margin-top:15vw;
}
img{
    height:150px;
    width:150px;
}
p{
    text-align:center;
}
#appName{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size:40px;
    margin-top:35px;
    margin-bottom:0px;
}
#motto{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size:23px;
    margin-top:0px;
}
#signBtn{
    display:flex;
    justify-content:center;
}
button{
    height:30px;
    width:100px;
}
button{
    background-color:#0078d4;
    color:white;
    border-top:#0078d4;
    border-bottom:#0078d4;
    border-left:#0078d4;
    border-right:#0078d4;
    font-weight:bold;
    font-size:20px;
    height:60px;
    width:180px;
    border-radius:5px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
button:hover{
    cursor:pointer;
    background-color:#1a8de6;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
